"use client";

/**
 * üéì Student Records Demo Page
 * Demonstrates AFUED Table Component with full features:
 * Server mode, sorting, search, selection, bulk actions, and export.
 */

import React, { useState, useEffect } from "react";
import { Table } from "@/components/ui/Table";
import { Badge } from "@/components/ui/Badge";
import { usePage } from "@/hooks/usePage";

export default function StudentRecordsPage() {
  const { setPage } = usePage();
  useEffect(() => {
    setPage("Student Records Demo");
  }, [setPage]);

  // üîÑ Local state
  const [students, setStudents] = useState<any[]>([]);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [page, setPageNumber] = useState(1);
  const [totalPages, setTotalPages] = useState(5);

  // üß± Columns
  const columns = [
    { header: "Student ID", accessorKey: "id" },
    { header: "Name", accessorKey: "name" },
    { header: "Department", accessorKey: "department" },
    { header: "Level", accessorKey: "level" },
    {
      header: "Status",
      accessorKey: "status",
      cell: ({ row }: any) => {
        const status = row.original.status;
        const variant =
          status === "Active"
            ? "success"
            : status === "Probation"
            ? "warning"
            : "error";
        return <Badge variant={variant}>{status}</Badge>;
      },
    },
    { header: "CGPA", accessorKey: "cgpa" },
  ];

  // ‚öôÔ∏è Simulate API call
  const fetchStudents = async ({
    page,
    pageSize,
    search,
    sortField,
    sortOrder,
  }: {
    page: number;
    pageSize: number;
    search?: string;
    sortField?: string;
    sortOrder?: "asc" | "desc";
  }) => {
    try {
      setIsLoading(true);
      setError(null);

      // Simulate network delay
      await new Promise((res) => setTimeout(res, 700));

      // Generate mock data (10 per request)
      const start = (page - 1) * pageSize + 1;
      const mock = Array.from({ length: pageSize }, (_, i) => ({
        id: `STU${String(start + i).padStart(3, "0")}`,
        name: ["Aloye Breakthrough", "Muna Love", "Joshua Okafor", "Tobi Hassan"][
          (start + i) % 4
        ],
        department: ["Computer Science", "Biology", "Mathematics", "Chemistry"][
          (start + i) % 4
        ],
        level: [100, 200, 300, 400][(start + i) % 4],
        status: ["Active", "Probation", "Inactive"][(start + i) % 3],
        cgpa: (Math.random() * 5).toFixed(2),
      }));

      // Simulate total pages
      setTotalPages(10);
      setStudents(mock);
      setIsLoading(false);
    } catch (err) {
      setError("Failed to load data");
      setIsLoading(false);
    }
  };

  // Initial load
  useEffect(() => {
    fetchStudents({ page, pageSize: 10 });
  }, [page]);

  // üß† Handle row selection + bulk actions
  const handleBulkAction = (action: string, selectedRows: any[]) => {
    if (action === "export") {
      alert(`Exporting ${selectedRows.length} selected rows to Excel...`);
    } else if (action === "delete") {
      alert(`Deleting ${selectedRows.length} selected students...`);
    }
  };

  return (
    <div className="max-w-6xl mx-auto p-6">
      <div className="bg-surface rounded-xl shadow-md p-4">
        <h2 className="text-2xl font-semibold mb-4 text-textPrimary">
          Student Records (Server Mode)
        </h2>

        <Table
          columns={columns}
          data={students}
          enableSearch
          enableSort
          enablePagination
          enableSelection
          enableExport
          serverMode
          pageSize={10}
          isLoading={isLoading}
          error={error}
          onBulkAction={handleBulkAction}
          onServerQuery={({ page }) => {
            setPageNumber(page);
            fetchStudents({ page, pageSize: 10 });
          }}
        />

        <p className="text-center text-sm text-textSecondary mt-4">
          Showing page {page} of {totalPages}
        </p>
      </div>
    </div>
  );
}
